import {Component} from "@angular/core";
import {IonicPage} from "ionic-angular";
import {SceneData} from '../../providers';

export const PAGES = {
  SHOPPING_LIST: 'ShoppingList',
  RECIPES: 'Recipes',
  BLOG: 'Blog'
};

@IonicPage()
@Component({
  selector: 'page-scene',
  templateUrl: 'scene.html',
})
export class Scene {

	scene: any = {};
  public shoppingListPage: string = PAGES.SHOPPING_LIST;
  public recipesPage: string = PAGES.RECIPES;
  public blogPage: string = PAGES.BLOG;

  constructor(public sceneData: SceneData) {
  	this.sceneData.getScene().then((data: any) => this.scene = data);
  }
}



